import React, { Component } from 'react';
import Titulo from './elementos/titulo';

class Educacion extends Component {
    render() {
        return (
            <div className="Educacion">
                <span class="esp">
                    <Titulo texto="Educacin" />
                </span>
                <span class="en">
                    <Titulo texto="Education" />
                </span>
                <b>Ingeniera Civil Informtica:</b> Universidad Tecnolgica INACAP Temuco. Titulado
                el ao 2011.<br /><br/>
                <b>Ingeniera en Informtica:</b> Universidad Tecnolgica INACAP Temuco.
                    Titulado el ao 2010.<br /><br />
                <b>Analista Programador:</b> INACAP Temuco 2006-2008. (Egresado).<br /><br />
                <b>Arquitectura:</b> 4 semestres Universidad Austral 2004-2005.<br/>
        </div>
        );
    }
}

export default Educacion;